import { Grid } from "@chakra-ui/react"
import Intro from "./Intro"
import Profile from "./Profile"
export default function About() {
  return (
    <Grid id="about-section">
      <Grid
        templateColumns={{ base: "1fr", sm: "1fr", md: "1fr", lg: "2fr 1fr" }}
        mt={{
          sm: "90px",
          md: "50px",
          lg: "80px",
        }}
        mb={{
          lg: "0px",
          sm: "-50px",
        }}
        p={{ base: "10px", sm: "35px", md: 5, lg: 10 }}
        gap={{ base: "10px", sm: "20px" }}
        pt={{ base: "10px", sm: "10px", md: 2, lg: 3 }}
        textAlign="left"
        justifyContent="center"
        alignItems="center"
        h={{
          lg: "300px",
        }}
        w={{ sm: "80%", base: "90%", md: "90%", lg: "100%" }}
        margin="auto"
      >
        <Intro />
        <Profile />
      </Grid>
      <Grid
        justifyContent="space-evenly"
        templateColumns={{
          base: "1fr",
          sm: "1fr",
          md: "1fr",
          lg: "1fr 1fr 1fr",
        }}
        gap={5}
        my={{ base: 0, sm: 10, md: 10, lg: 10 }}
      ></Grid>
    </Grid>
  )
}
