import {
  Box,
  Flex,
  HStack,
  IconButton,
  Button,
  Stack,
  useDisclosure,
  Text,
} from "@chakra-ui/react"
import { HamburgerIcon, CloseIcon } from "@chakra-ui/icons"
import Resume from "../Resources/Resume.pdf"
import Styles from "./Navbar.module.css"
import { Link } from "react-scroll"

export default function Navbar1() {
  const { isOpen, onOpen, onClose } = useDisclosure()

  const handleDownloadNewPageResume = () => {
    const newWindow = window.open(
      "https://drive.google.com/file/d/1nW47gP8MWnxdJDuMj8fhMz-Z-DXyyn3M/view?usp=sharing",
      "_blank",
      "noopener,noreferrer"
    )

    if (newWindow) newWindow.opener = null

    fetch(Resume).then((response) => {
      response.blob().then((blob) => {
        const fileURL = window.URL.createObjectURL(blob)
        let alink = document.createElement("a")
        alink.href = fileURL
        alink.download = "fp04_159_Mohtashim_Mir_Resume.pdf"
        alink.click()
      })
    })
  }

  return (
    <>
      <Box className={Styles.navbar} px={4} h="90px" paddingTop={3}>
        <Flex h={16} alignItems={"center"} justifyContent={"space-between"}>
          <IconButton
            size={"sm"}
            icon={
              isOpen ? (
                <CloseIcon color={"black"} />
              ) : (
                <HamburgerIcon color={"black"} />
              )
            }
            aria-label={"Open Menu"}
            display={{ md: "none" }}
            onClick={isOpen ? onClose : onOpen}
          />
          <HStack spacing={10} alignItems={"center"}>
            <Text
              display={{
                base: "none",
                sm: "none",
                md: "flex",
                lg: "flex",
              }}
              id="logo"
            >
              Mohtashim Mir
            </Text>
            <Text
              spacing={10}
              alignItems={"center"}
              display={{
                base: "flex",
                sm: "flex",
                md: "none",
                lg: "none",
              }}
              id="logo"
            >
              Mohtashim
            </Text>
            <Link
              to="about-section"
              smooth
              duration={500}
              activeClass="active"
              spy={true}
              offset={-100}
              style={{ color: "white" }}
            ></Link>
            <HStack
              as={"nav"}
              spacing={4}
              display={{ base: "none", md: "flex" }}
            >
              <Link
                to="about-section"
                smooth
                duration={500}
                activeClass="active"
                spy={true}
                offset={-100}
                style={{ color: "white" }}
              >
                <Button
                  bg="#2253c8"
                  textStyle="Navbar"
                  p="12px"
                  _hover={{
                    backgroundColor: "#fafcf9",
                    borderRadius: "10px",
                    color: "black",
                  }}
                >
                  About
                </Button>
              </Link>

              <Link
                to="skills-section"
                spy={true}
                offset={-100}
                smooth
                duration={500}
                style={{ color: "white" }}
              >
                <Button
                  bg="#2253c8"
                  textStyle="Navbar"
                  p="12px"
                  _hover={{
                    backgroundColor: "#fafcf9",
                    borderRadius: "10px",
                    color: "black",
                  }}
                >
                  Skills
                </Button>
              </Link>

              <Link
                to="projects-section"
                smooth
                duration={500}
                spy={true}
                style={{ color: "white" }}
                offset={-100}
              >
                <Button
                  bg="#2253c8"
                  textStyle="Navbar"
                  p="12px"
                  _hover={{
                    backgroundColor: "#fafcf9",
                    borderRadius: "10px",
                    color: "black",
                  }}
                >
                  Projects
                </Button>
              </Link>

              <Link
                to="contact-section"
                spy={true}
                offset={-100}
                smooth
                duration={500}
                style={{ color: "white" }}
              >
                <Button
                  bg="#2253c8"
                  textStyle="Navbar"
                  p="12px"
                  _hover={{
                    backgroundColor: "#fafcf9",
                    borderRadius: "10px",
                    color: "black",
                  }}
                >
                  Contacts
                </Button>
              </Link>
            </HStack>
          </HStack>
          <Flex alignItems={"center"}>
            <Link
              onClick={handleDownloadNewPageResume}
              to="https://drive.google.com/file/d/1nW47gP8MWnxdJDuMj8fhMz-Z-DXyyn3M/view?usp=sharing"
              target="_blank"
              download
              cursor="pointer"
              style={{ color: "white" }}
            >
              <Button
                variant="outline"
                bg="#2253c8"
                mr={7}
                letterSpacing={1}
                _hover={{
                  backgroundColor: "#fafcf9",
                  color: "black",
                }}
              >
                Resume
              </Button>
            </Link>
          </Flex>
        </Flex>

        {isOpen ? (
          <Box
            pb={4}
            w="100%"
            display={{ md: "none" }}
            padding="15px"
            color={{ sm: "color:black" }}
            bgColor="#2253c8"
          >
            <Stack as={"nav"} spacing={4}>
              <Link
                to="about-section"
                smooth
                duration={500}
                activeClass="active"
                spy={true}
                offset={-100}
              >
                <Button
                  bg="#2253c8"
                  textStyle="Navbar"
                  p="12px"
                  _hover={{
                    backgroundColor: "#fafcf9",
                    borderRadius: "10px",
                    color: "black",
                  }}
                >
                  About
                </Button>
              </Link>

              <Link
                to="skills-section"
                spy={true}
                offset={-100}
                smooth
                duration={500}
              >
                <Button
                  bg="#2253c8"
                  textStyle="Navbar"
                  p="12px"
                  _hover={{
                    backgroundColor: "#fafcf9",
                    borderRadius: "10px",
                    color: "black",
                  }}
                >
                  Skills
                </Button>
              </Link>

              <Link
                to="projects-section"
                smooth
                duration={500}
                spy={true}
                offset={-100}
              >
                <Button
                  bg="#2253c8"
                  textStyle="Navbar"
                  p="12px"
                  _hover={{
                    backgroundColor: "#fafcf9",
                    borderRadius: "10px",
                    color: "black",
                  }}
                >
                  Projects
                </Button>
              </Link>

              <Link
                to="contact-section"
                spy={true}
                offset={-100}
                smooth
                duration={500}
              >
                <Button
                  bg="#2253c8"
                  textStyle="Navbar"
                  p="12px"
                  _hover={{
                    backgroundColor: "#fafcf9",
                    borderRadius: "10px",
                    color: "black",
                  }}
                >
                  Contacts
                </Button>
              </Link>
            </Stack>
          </Box>
        ) : null}
      </Box>
    </>
  )
}
