import { Box, Button, Text } from "@chakra-ui/react"

export default function Intro() {
  return (
    <Box
      w="100%"
      mt={{
        base: "90px",
        sm: "20px",
        md: "30px",
        lg: "90px",
      }}
    >
      <Text
        fontSize={{ base: "2xl", sm: "3xl", md: "4xl", lg: "4xl" }}
        color="grey"
        letterSpacing="10px"
      >
        HELLO
      </Text>
      <Text
        id="name"
        fontSize={{ base: "2xl", sm: "2xl", md: "4xl", lg: "5xl" }}
      >
        {" "}
        I'm <span>Mohtashim Mir</span>{" "}
      </Text>
      <Text
        fontSize={{ base: "2xl", sm: "3xl", md: "4xl", lg: "3xl" }}
        color="#2253C8"
      >
        Full Stack Web Developer
      </Text>
      <Text fontSize={{ base: "sm", sm: "lg", md: "xl", lg: "lg" }}>
        Motivated and efficient Full-stack web developer with a history of
        education in Civil Engineering and an interest in coding. Working with
        MERN stack and programming languages like JavaScript catalyzed my
        curiosity for web development. I have worked in collaborative
        environments demonstrating my coding and interpersonal skills to meet
        deadlines. Looking forward for some interesting and challenging
        opportunities.{" "}
      </Text>
      <Text
        fontSize={{ base: "md", sm: "lg", md: "xl", lg: "xl" }}
        color="grey"
      ></Text>
      <a
        href={require("../Resources/Resume.pdf")}
        download="fp04_159-Mohtashim-Mir-Resume"
      >
        <Button
          variant="outline"
          id="resume"
          _hover={{ bg: "black", color: "white" }}
          letterSpacing={2}
        >
          RESUME
        </Button>
      </a>
    </Box>
  )
}
